<template>
  <div class="tile">
    <card-component class="tile is-child is-4" title="项目分页" icon="projector">
      <div>
        <nav class="level margin-0">
          <div v-for="item in protasknav" :key="item.name" class="level-item has-text-centered">
            <div>
              <p class="heading">{{ item.data.label }}</p>
              <p class="title">{{ item.data.value }}</p>
            </div>
          </div>
        </nav>
        <b-progress type="is-cred-2" :value="80" class="margin-05" size="is-small" />
        <div class="card">
          <b-tabs v-model="defaulttab">
            <b-tab-item label="PC Soft" icon="laptop">
              <project-total-table :data="protask" :showprocess="false" />
            </b-tab-item>
            <b-tab-item label="Archone" icon="arch">
              <project-total-table :data="protask" :showprocess="false" />
            </b-tab-item>
          </b-tabs>
        </div>
      </div>
    </card-component>
    <card-component class="tile is-child is-3" title="微型看板" icon="microwave">
      <!--微型看板的内容估计直接用hero的东西，但内容和展示肯定是重写的-->
      <hero :heroinfo="heroinfo" />
    </card-component>
    <card-component class="tile is-child is-3" title="个人活跃度" icon="chart-arc">
      <div class="pl-5" style="height:160px">
        <apexchart :options="heatmapchart.Options" :series="heatmapchart.series" />
      </div>
      <div>
        <apexchart :options="timelinechart.Options" :series="timelinechart.series"></apexchart>
      </div>
    </card-component>
    <card-component class="tile is-child" title="任务执行" icon="chart-arc">
      <div v-for="(item, index) in exectask" :key="index">
        <div class="level mt-4">
          <vue-ellipse-progress class="level-item" :progress="item.value" :size="110" color="#75cfb8">
            <span slot="legend-caption">{{ item.type }}</span>
          </vue-ellipse-progress>
        </div>
      </div>
    </card-component>
  </div>
</template>

<script>
import ProjectTotalTable from '@/components/Table/ProjectTotalTable'
import CardComponent from '@/components/Card/CardComponent'
import Hero from '@/components/Tile/Hero'
import { hero, personaltaskbyproject, persionaltasknav, exectask, persionalsubmit } from '../../scripts/common'
import { getYestdayTaskInfo } from '../../scripts/common'
import { COSTUMERCOLOR, makeEemu } from '../../scripts/internal'
export default {
  name: 'WelcomeTile',
  components: {
    CardComponent,
    ProjectTotalTable,

    Hero
  },
  data() {
    return {
      /**
       * UPDATE: 2021-02-05 基于个人，按照项目展示
       * 项目信息
       */
      protask: personaltaskbyproject,

      // 更新个人任务的tab字段头等
      protasknav: persionaltasknav,

      /**
       * 默认显示tab
       */
      defaulttab: 0,

      /**
       * 欢迎的用户信息
       */
      heroinfo: hero,

      /**
       * 按照任务类型分拆的执行任务
       */
      exectask: exectask,

      /**
       * 热力图数据以及配置
       */
      heatmapchart: {
        series: persionalsubmit,
        Options: {
          chart: {
            height: 150,
            type: 'heatmap',
            toolbar: {
              show: false
            }
          },
          grid: {
            padding: {
              top: -30,
              bottom: 100,
              right: 80
            }
          },
          dataLabels: {
            enabled: false
          },
          colors: ['#16c79a'],
          title: {
            text: '提交记录'
          }
        }
      },
      /**
       * 时间线数据以及配置
       */
      timelinechart: {
        series: [
          {
            data: []
          }
        ],
        Options: {
          chart: {
            type: 'rangeBar',
            toolbar: {
              show: false
            }
          },
          plotOptions: {
            bar: {
              horizontal: true,
              distributed: true,
              dataLabels: {
                hideOverflowingLabels: false
              }
            }
          },
          dataLabels: {
            enabled: false,
            style: {
              colors: ['#f3f4f5', '#fff']
            }
          },
          xaxis: {
            type: 'numeric'
          },
          yaxis: {
            show: false
          },
          grid: {
            row: {
              colors: ['#fafafa', '#fff'],
              opacity: 1
            }
          },
          title: {
            text: 'JOB执行时间(TOP 10)'
          }
        }
      }
    }
  },
  mounted() {
    this.seriesFunc()
  },
  methods: {
    /**
     * 数据处理
     */
    seriesFunc() {
      this.timelinechart.series[0] = {
        name: '',
        data: []
      }
      const temparray = getYestdayTaskInfo()
      // 组装数据
      const colors = makeEemu(COSTUMERCOLOR)
      temparray.forEach(element => {
        const colorindex = Math.floor(Math.random() * colors.length) // 随机状态
        const singledata = {
          x: element.taskname,
          y: element.usetime,
          fillColor: colors[colorindex]
        }
        this.timelinechart.series[0].data.push(singledata)
      })
      // console.log(this.timelinechart)
    }
  }
}
</script>

<style scoped>
.margin-0 {
  margin: 0rem;
}
.margin-05 {
  margin: 0.5rem;
}
</style>
